import { CSSProperties, PropsWithChildren } from "react"
import { styled } from "@rp/react/theme"

const SubHeaderStyled = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.grey[700],
  color: "white",
  display: "flex",
  height: 40,
  width: "100%",
}))

type Props = PropsWithChildren & { styles?: CSSProperties }

const SubHeader = ({ children, styles = {} }: Props) => (
  <SubHeaderStyled style={styles}>{children}</SubHeaderStyled>
)

export { SubHeader }
